<template>
	<div id="msg" class="warp">
		<!--头部按钮组 start-->
		<div class="btn-group clearfix">
			<span class="send">发送状态</span>
			<el-select v-model="value" placeholder="请选择" @change='change'>
				<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
				</el-option>
			</el-select>
			<button type="button" class="del right" @click="multipleDeletePrinter">删除</button>
			<router-link :to="{path:'/message/edit'}">
				<button type="button" class="add right">发消息</button>
			</router-link>
			<router-link :to="{path:'/message/drafts'}">
				<button type="button" class="add draft right">草稿({{count}}) </button>
			</router-link>

		</div>
		<!--头部按钮组 end-->
		<!--表格列表 start-->
		<div class="msglist">
			<el-table   @sort-change="sort" height="680"  stripe :data='msgList' @selection-change="handleSelectionChange">
				<el-table-column type="selection" min-width="52">
				</el-table-column>
				<el-table-column label="标题" align="center" min-width="650">
					<template slot-scope="scope">
						<span>{{ scope.row.title }}</span>
					</template>
				</el-table-column>
				<el-table-column sortable="true" label="发送时间" align="center" min-width="330"  prop="sort_send_time">
					<template slot-scope="scope">
						<span>{{scope.row.send_time}}</span>
					</template>
				</el-table-column>
				<el-table-column label="发送状态" align="center" min-width="300">
					<template slot-scope="scope">
						<span>{{send[scope.row.is_send]}}</span>
					</template>
				</el-table-column>
			</el-table>
			<!-- 分页 starat -->
			<div class="pagination-container" ref='page'>
				<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="listQuery.page" :page-sizes="[10,20,40]" :page-size="listQuery.limit" layout="sizes, jumper, prev, pager, next" :total=total>
				</el-pagination>
			</div>
			<!-- 分页 end -->
		</div>
		<!--表格列表 end-->
		<!--保存成功失败 Tips start-->
		<div class="tips" v-if='showTips'>
			<span class="info">{{msg}}</span>
		</div>
		<!--保存成功失败 Tips end-->
		<!--删除消息模态框 start-->
		<div class="mask" v-if="showDelbox" @click="close"></div>
		<div class="dialog" v-if="showDelbox">
			<span class="line"></span>
			<p class="info">删除待发送消息，消息到达发送时间将不再发出，确认删除？</p>
			<div class="bottom">
				<button type="button" class="bl" @click="confm">确定</button>
				<button type='button' class="adds" @click="close">取消</button>
			</div>
		</div>
		<!--删除消息模态框 end-->
		<!--暂无数据样式 start-->
		<div class="empty" v-if='showEmpty'>
			<div class="imgs">
				<img src="../img/empty.png" alt="" />
			</div>
			<span>暂无数据...</span>
		</div>
		<!--暂无数据样式 end-->
	</div>
</template>

<script>
	import moment from "moment";
	import { getSysmsg, Delmsg } from '../api';
	import { ls } from "@/config/pageStore";
	import { Comethods } from '../index';
	import { Sendstatus } from "@/config/cfg";
	export default {
		data() {
			return {
				msgList: [], //保存请求回来的消息数据
				testList: [],//用于分页list过滤
				total: 40,  //总条目数
				listQuery: {
					page: 1,
					limit: 10,
					sort:'+id'
				},
				multipleSelection: [], //存放多选选中数据
				selectIndex: {}, //记录某页的勾选项
				//渲染下拉框的全部 已发送 待发送 
				options: [{
					value: Sendstatus.ALL,
					label: Sendstatus.toString(Sendstatus.ALL)
				}, {
					value: Sendstatus.YES,
					label: Sendstatus.toString(Sendstatus.YES)
				}, {
					value: Sendstatus.NO,
					label: Sendstatus.toString(Sendstatus.NO)
				}],
				value: '',  //保存下拉框选中的值
				send: '',  //发送状态
				status: '',//选中的发送状态
				count: '', //可发消息计数
				showTips: false, //控制显示Tips提示框
				msg: '', //提示框显示的内容
				showDelbox:false, //控制显示删除模态框
				showEmpty:false, //控制显示无数据的样式
			//排序
			    isSort: false,
			    sortLabel: "",
	     		sortOrder: 1
			}
		},
		created() {
			this.initTableData();
			this.getDraft();
		},
		computed: {
			Sendstatus() {
				return Sendstatus;
			}
		},
		methods: {
			//获取数据
			getDraft() {
				getSysmsg({
					news_list: 1,
					is_draft: 1
				}, (resp) => {
					let msgList = resp.data.list || [];
					this.count = msgList.length;
					if(this.count > 99) {
						this.count = '99+';
					}
				});
			},
			//初始化表格信息
			initTableData() {
				//获取每页选择显示的条目数的历史记录
				if(ls.getItem("itemNum") != null) {
					this.listQuery.limit = Number(ls.getItem("itemNum"));
				}
				//获取每页选择页面的历史记录
				if(sessionStorage.getItem("pageNum") != null) {
					this.listQuery.page = Number(sessionStorage.getItem("pageNum"));
				}
				let data = {
						news_list: 1,
						is_send: this.status
				 };
					if(this.isSort) {
						data[this.sortLabel] = this.sortOrder;
					}
					getSysmsg(data, this.getListHandler);
			},
			//获取数据后成功的回调
			getListHandler(resp){
			 if(resp.ret === 0){
			 	this.send = Sendstatus.code;
					this.msgList = resp.data.list || [];
					let page = this.$refs.page;
					this.msgList = this.msgList.map(item => {
						if(item.send_time) {
							item['send_time'] = moment(item.send_time * 1000).format(
								"YYYY-MM-DD HH:mm:ss"
							);
						}
						return item;
					});
					if(this.msgList.length === 0){
						this.showEmpty = true;
						page.style.display = "none";
					}else{
						page.style.display = "block";
					}
					this.testList = resp.data.list || [];
					this.total = Comethods.getTotal(this.testList);
					this.msgList = Comethods.getPageList(this.testList, this.listQuery.limit, this.listQuery.page);
			 }
			},
			//下拉框change事件
			change(val) {
				this.status = val;
				if(val == Sendstatus.ALL) {
					this.status = '';
					this.showEmpty = false;
				}
				if(val == Sendstatus.NO){
					this.showEmpty = false;
				}
				if(val == Sendstatus.YES){
					this.showEmpty = false;
				}
				this.initTableData();
			},
			//分页事件处理，条数和当前页数改变触发
			handleSizeChange(val) {
				this.listQuery.limit = val;
				ls.setItem("itemNum", this.listQuery.limit);
				this.msgList = Comethods.getPageList(this.testList, this.listQuery.limit, this.listQuery.page);
				this.total = Comethods.getTotal(this.testList);
			},
			//目标页
			handleCurrentChange(val) {
				this.listQuery.page = val;
				this.total = Comethods.getTotal(this.testList);
				this.msgList = Comethods.getPageList(this.testList, this.listQuery.limit, this.listQuery.page);
				let allSelectIndex = this.selectIndex[val];
				let indexArr = [];
				if(allSelectIndex !== undefined) {
					for(let i in this.selectIndex[val]) {
						let index = Number(this.selectIndex[val][i] + 1);
						$(".el-checkbox__input").eq(index).addClass("is-checked");
					}
				}
				sessionStorage.setItem("pageNum", this.listQuery.page);
			},
			//多选
			handleSelectionChange(val) {
				this.multipleSelection = val;
			},
			//删除消息
			multipleDeletePrinter() {
				if(this.multipleSelection.length == 0) {
					this.showTips = true;
					this.msg = '未选中任何消息';
					setTimeout(()=>{
						this.showTips = false;
					},1500);
					
					return;
				}
				let msgArr = this.multipleSelection.map(function(item) {
					return item.news_id;
				});
				this.multipleSelection.forEach(item=>{
					if(item.is_send === Sendstatus.NO){
						this.showDelbox = true;
					}else{
						let data = {
						del_news: 1,
						news_id_list: msgArr
					};
					Delmsg(data, this.opearteHandler);
					}
				});
				
				
			},
			//删除成功后的处理
			opearteHandler(resp) {
				if(resp.ret === 0) {
					this.showTips = true ;
					this.msg = '删除成功';
					this.initTableData();
					setTimeout(()=>{
						this.showTips = false;
					},1500);
				} else {
					this.showTips = true ;
					this.msg = '删除失败';
					setTimeout(()=>{
						this.showTips = false;
					},1500);
				}
			},
			//多选删除
			confm(){
				this.showDelbox = false;
				let msgArr = this.multipleSelection.map(function(item) {
					return item.news_id;
				});
				let data = {
						del_news: 1,
						news_id_list: msgArr
					};
					Delmsg(data, this.opearteHandler);	
			},
			//关闭删除模态框
			close(){
				this.showDelbox = false;
			},
			//排序
			sort(item) {
			      this.sortLabel = item.prop;
			
			      if (item.order === "descending") {
			        this.isSort = true;
			        this.sortOrder = -1;
			      } else if (item.order === "ascending") {
			        this.isSort = true;
			        this.sortOrder = 1;
			      } else {
			        this.isSort = false;
			      }
			
			      this.initTableData();
			    }
		}
	}
</script>

<style lang="scss" scoped>
	@import 'src/styles/mixin.scss';
	.warp {
		width: 100%;
		height: 100%;
		background: #fff;
		position:relative;
		.empty{
		position: absolute;
		left: 0;
		top: 120px;
		width: 100%;
	    height:610px;
	    background: #fff;
	    text-align: center;
	    padding-top: 189px;
	    span{
	    	@include fc(14px,#9b9b9b);
	    }
	}
		.btn-group {
			padding: 20px 0;
			background: #fff;
			.send {
				@include fc(14px, #000);
				font-weight: 400;
				padding: 0 20px 0 14px;
			}
			.add {
				@include wh(100px, 34px);
				@include fc(14px, #4877E7);
				background: #fff;
				border: 1px solid #4877e7;
			}
			.left {
				margin-left: 14px;
			}
			.del {
				@include wh(100px, 34px);
				@include fc(14px, #E7487E);
				border: 1px solid #E7487E;
				background: #fff;
				margin: 0 14px 0 20px;
			}
			.draft {
				margin-right: 20px;
			}
		}
	}
	.tips {
			position: fixed;
			@include wh(360px, 100px);
			background: #000;
			opacity: 0.8;
			left: 50%;
			top: 50%;
			margin-left: -180px;
			margin-top: -50px;
			text-align: center;
			border-radius: 2px;
			.info {
				display: inline-block;
				@include fc(14px, #fff);
				padding-top: 40px;
				letter-spacing: 0.5;
			}
		}
		.mask{
			position: fixed;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			z-index: 1001;
			background:#000;
			opacity: 0.5;
		}
		.dialog {
			position:absolute;
			z-index: 1002;
			left: 50%;
			top: 50%;
			margin-left: -270px;
			margin-top: -140px;
			@include wh(540px,280px);
			background: #fff;
			.line{
				display:block;
				height:4px;
				background:#5A8CFF;
			}
			.info{
				margin-top: 80px;
				margin-left: 101px;
				@include fc(14px,#666);
				position:relative;
				&:before{
					position: absolute;
					content: '';
					background: url(../img/question_img_normal.png)no-repeat;
					@include wh(50px,50px);
					left: -69px;
   					top: -14px;
				}
			}
			.bottom{
				font-size: 0;
				margin: 90px 80px 0;
				.bl{
				@include wh(160px,40px);
				@include fc(14px, #fff);
				background: #4877E7;
				border: 1px solid #4877e7;
				border-radius: 2px;
		}
		.adds{
				@include wh(160px, 40px);
				@include fc(14px, #4877E7);
				background: #fff;
				border: 1px solid #4877e7;
				border-radius: 2px;
				margin-left:60px;
		}
			}
		}
	
	button {
		cursor: pointer;
	}
</style>
<style lang="scss">
	@import 'src/styles/mixin.scss';
	#msg {
		.pagination-container {
			text-align: center;
			padding: 50px 14px 20px;
		}
		.btn-group {
			.el-input__inner {
				border-radius: 0;
			}
			.el-input__inner {
				@include wh(180px, 34px);
			}
		}
		a {
			color: #333;
		}
		.el-table__empty-block{
			display: none;
		}
		.el-table__empty-text{
			display: none;
		}
		.el-table td{
			height: 50px;
		}
	}
</style>


